import service from '../httpService'

const vendor = {
    namespaced: true,
    state: {
        vendors: [],
    },
    mutations: {
        SET_VENDORS: (state, vendors) => {
            state.vendors = vendors
        }
    },
    actions: {
        async fetchVendors ({ commit }, payload) {
            try {
                commit('SET_LOADER', true, { root: true })
                 await service.getVendors(payload).then(response => {
                    if (response.status === 200 && response.data.result) {
                        commit('SET_VENDORS', response.data.result)
                    } else {
                        commit('SET_VENDORS', [])
                    }
                }).finally(() => { commit('SET_LOADER', false, { root: true }) })
            } catch (error) {
                commit('SET_VENDORS', [])
                commit('SET_LOADER', true, { root: true })
            }
        },
        async updateAuthorize ({ commit, dispatch }, payload) {
            try {
                commit('SET_LOADER', true, { root: true })
                await service.updateAuthorize(payload.data).then(response => {
                    if (response.status === 200 && response.data.message == 'SUCCESS') {
                        commit('SET_SNAKBAR', { msg: `${payload.data.authorization_status == 0 ? 'Un Authorized' : 'Authorized'} Successfully`, show: true, color: 'teal darken-1', timeout: 2500 }, { root: true })
                        dispatch('fetchVendors', { client_id: payload.client_id })
                    } else {
                        commit('SET_SNAKBAR', { msg: response.data.stat, show: true, color: 'teal darken-1', timeout: 2500 }, { root: true })
                    }
                }).finally(() => { commit('SET_LOADER', false, { root: true }) })
            } catch (error) {
                commit('SET_SNAKBAR', { msg: error, show: true, color: 'teal darken-1', timeout: 2500 }, { root: true })
                commit('SET_LOADER', true, { root: true })
            }
        }
    },
    getters: {
        getVendors: state => state.vendors
    }
}

export default vendor